;\n\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      padding: 0 ms(2) ms(0);\n    }\n\n    &--button {\n      font-size: ms(-1);\n      margin-left: auto;\n\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n        font-size: ms(0);\n      }\n    }\n  }\n\n  &--logo {\n    display: block;\n    width: ms(6);\n    height: auto;\n    margin-right: ms(-4);\n\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      margin-right: ms(-2);\n    }\n  }\n\n  &--title {\n    font-weight: 400;\n    font-size: ms(1);\n    color: $dark-gray;\n\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      font-size: ms(2);\n    }\n  }\n\n  &--subtitle {\n    font-weight: 700;\n    margin-bottom: 0;\n    font-size: ms(-1);\n    color: $dark-gray;\n\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      margin-bottom: ms(-6);\n      font-size: ms(0);\n    }\n  }\n\n  &--controls {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    padding: ms(-2) ms(0) ms(-2) ms(2);\n  }\n\n  &--back {\n    display: block;\n    color: $gray;\n    text-transform: lowercase;\n    font-size: ms(-1);\n  }\n\n  &--postcode {\n    margin-left: auto;\n    position: relative;\n\n    &--input {\n      padding-right: ms(6);\n      font-size: ms(-1);\n      width: 11em;\n      line-height: 1.6;\n      border: 1px solid $medium-gray;\n      border-radius: 2em;\n      padding: ms(-5) ms(-2);\n\n      &::placeholder {\n        color: $new-gray;\n      }\n\n      &:focus {\n        outline: none;\n        box-shadow: none;\n        border-color: $gray;\n      }\n    }\n\n    &--button {\n      position: absolute;\n      right: 1px;\n      top: 1px;\n      line-height: 1.4;\n      background-color: $brand-blue;\n      color: white;\n      border: 1px solid $medium-gray;\n      border-radius: 2em;\n      padding: ms(-4);\n      svg {\n        width: ms(0);\n        height: ms(0);\n      }\n    }\n\n    &--clear {\n      margin-left: ms(0);\n      color: black;\n    }\n\n    &--error {\n      padding: ms(-1) ms(0);\n      font-size: ms(-1);\n      border: 1px solid $red;\n      border-radius: 3em;\n      background-color: lighten($red, 30%);\n      margin: 0 ms(0) ms(-2) ms(2);\n      color: $red;\n    }\n\n    &--badge {\n      padding: ms(-5) ms(-1);\n      border: 1px solid $gray;\n      border-radius: 2em;\n      font-size: ms(-1);\n      background-color: $light-gray;\n      color: $gray;\n    }\n  }\n\n  &--menu {\n    flex: 1;\n    flex-direction: column;\n    display: flex;\n\n    &--item {\n      overflow: hidden;\n      display: flex;\n      flex-direction: column;\n      flex: 0 0 4em;\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      padding: 0 ms(2);\n\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n      }\n\n      // &:not(:last-child) {\n      border-top: 1px solid $light-gray;\n      // }\n\n      @extend .expand-enter-active;\n\n      &.contract {\n        flex: 0 0 0;\n        opacity: 0;\n        user-select: none;\n        pointer-events: none;\n\n        @extend .expand-leave-active;\n      }\n\n      &.router-link-active {\n        background-color: $brand-green;\n\n        &:hover {\n          background-color: darken($brand-green, 5%);\n        }\n\n        .sidebar--menu--item--icon {\n          filter: brightness(0.5);\n          opacity: 0.7;\n        }\n\n        .sidebar--menu--item--arrow {\n          transform: rotate(90deg);\n        }\n      }\n\n      &:hover {\n        background-color: $light-gray;\n      }\n\n      &--icon {\n        width: ms(4);\n        height: ms(4);\n        object-fit: contain;\n        object-position: center;\n        display: block;\n        margin-right: ms(1);\n        color: $brand-light-blue;\n      }\n\n      &--arrow {\n        width: ms(4);\n        height: ms(4);\n        margin-left: auto;\n        color: $gray;\n        transform-origin: 50% 50%;\n        transition: transform $base-duration $base-timing;\n      }\n    }\n  }\n}\n",".fade-enter-active, .fade-leave-active {\n  transition: opacity 0.5s;\n}\n.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {\n  opacity: 0;\n}\n\n.popup-enter-active, .popup-leave-active {\n  transition: transform 0.75s;\n  transition-timing-function: ease-in;\n}\n\n.popup-leave-active {\n  transition-timing-function: ease-out;\n  transition-delay: 0.5s;\n}\n\n.popup-enter, .popup-leave-to /* .fade-leave-active below version 2.1.8 */ {\n  transform: translateY(100%);\n  z-index: 999999;\n}\n\n.popup-leave, .popup-enter-to /* .fade-leave-active below version 2.1.8 */ {\n  transform: translateY(0);\n  z-index: 999998;\n}\n\n\n.slide-enter-active, .slide-leave-active {\n  transition: transform 0.75s;\n}\n\n.slide-leave-active {\n  z-index: 9999 !important;\n}\n\n.slide-enter /* .fade-leave-active below version 2.1.8 */ {\n  transform: translateX(100%);\n}\n\n.slide-leave-to {\n  transform: translateX(-100%);\n}\n\n.slide-from-right-enter-active, .slide-from-right-leave-active {\n  transition: transform 1.7s;\n}\n\n.slide-from-right-enter {\n  transform: translateX(1000px);\n}\n\n\n.expand-enter-active {\n  transition: flex 0.6s, opacity 0.6s;\n}\n\n.expand-leave-active {\n  transition: flex 0.6s, opacity 0.6s;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n  user-select: none;\n  pointer-events: none;\n  flex: 0 0 0px !important;\n}\n","@import '../../../node_modules/modularscale-sass/stylesheets/modularscale';\n@import 'fonts';\n@import 'variables';\n@import 'transitions';\n\n* {\n  font-family: 'Nunito Sans', sans-serif;\n}","$modularscale: (\n  base: 1rem,\n  ratio: 1.2\n);\n\n$sidebar-width: 360px;\n$sidebar-max-width: 30%;\n\n$light-gray: #f8f8f8;\n$medium-gray: #dfdfe5;\n$new-gray: #afafbf;\n$gray: #85898E;\n$dark-gray: #444455;\n\n$black: #222;\n\n$brand-green: #9bdbcd;\n$brand-pink: #efbfd3;\n$brand-blue: #48767f;\n\n$brand-light-blue: #abcee2;\n\n$blue: #aecdeb;\n$light-blue: #f0f6fc;\n$red: #df7839;\n$orange: #f4d582;\n$cream: #fef6dd;\n\n$base-timing: ease;\n$base-duration: 500ms;\n\n$base-radius: 0.25rem;\n\n$box-shadow: 0 0.75em 1.2em -0.125em rgba($black, 0.5), 0 0px 0 1px rgba($black, 0.02);"],"sourceRoot":""}]);
