ft: 0;\n  position: absolute;\n  overflow-y: auto;\n  padding: 0 0 ms(2);\n  top: 0;\n  bottom: 0;\n  right: 0;\n  z-index: 9999999;\n  background-color: white;\n\n  @media screen and (orientation: landscape) and (min-width: 800px) {\n    padding: 0 ms(4) ms(2);\n    left: $sidebar-width;\n  }\n\n  .container {\n    max-width: 800px;\n    margin: 0 auto;\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      margin: ms(6) auto;\n    }\n  }\n\n  .panel {\n    margin-top: ms(4);\n    padding-top: ms(4);\n    border-top: 1px solid $medium-gray;\n  }\n\n  .panel-heading {\n    font-size: ms(2);\n    font-weight: 700;\n  }\n\n  .entry-title {\n    font-size: ms(3);\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      font-size: ms(5);\n    }\n    font-weight: 700;\n    margin-bottom: 0;\n\n    span {\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n        display: inline-block;\n        margin-right: ms(1);\n      }\n    }\n\n    small {\n      font-weight: 400;\n      display: block;\n      color: $gray;\n      font-size: ms(2);\n      vertical-align: baseline;\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n        display: inline-block;\n      }\n    }\n  }\n\n  .entry-address {\n    display: inline-block;\n    font-size: ms(0);\n    margin-top: ms(-4);\n\n    &::before, &::after {\n      content: '';\n      display: inline-block;\n      background-image: url(/assets/images/marker-icon-red.svg);\n      width: 0.75em;\n      height: 1em;\n      margin-right: .25em;\n      background-size: contain;\n      background-repeat: no-repeat;\n      background-position: left bottom;\n    }\n\n    &::after {\n      background-image: url(/assets/images/external_link.svg);\n      margin-left: 0.25em;\n    }\n  }\n\n  .entry-description {\n    margin-top: ms(2);\n    margin-bottom: ms(2);\n  }\n\n  .image-container {\n    position: relative;\n    background-image: linear-gradient(to top, $blue 30%, white 30%);\n    &.is-red {\n      background-image: linear-gradient(to top, $red 30%, white 30%);\n    }\n    &.is-orange {\n      background-image: linear-gradient(to top, $orange 30%, white 30%);\n    }\n    &.is-cream {\n      background-image: linear-gradient(to top, $cream 30%, white 30%);\n    }\n\n    & > div {\n      height: 100%;\n    }\n    margin-bottom: ms(2);\n    padding-bottom: ms(4);\n    height: calc(100vw - #{2 * ms(2)});\n    @media screen and (orientation: landscape) and (min-width: 800px) {\n      height: 0.5 * 800px;\n      background-image: linear-gradient(to top, $blue 40%, white 40%);\n      &.is-red {\n        background-image: linear-gradient(to top, $red 40%, white 40%);\n      }\n      &.is-orange {\n        background-image: linear-gradient(to top, $orange 40%, white 40%);\n      }\n      &.is-cream {\n        background-image: linear-gradient(to top, $cream 40%, white 40%);\n      }\n    }\n\n    img {\n      margin: 0 auto;\n      height: 100%;\n      width: 75vw;\n      object-fit: contain;\n      object-position: center bottom;\n\n      @media screen and (orientation: landscape) and (min-width: 800px) {\n        height: 0.5 * 800px;\n        width: 0.5 * 800px;\n        padding-bottom: ms(2);\n      }\n      &.is-square {\n        width: 60vw;\n\n        @media screen and (orientation: landscape) and (min-width: 800px) {\n          width: 0.4 * 800px;\n        }\n      }\n\n      &.is-x-wide {\n        width: calc(100vw - 3 * ms(2));\n\n        @media screen and (orientation: landscape) and (min-width: 800px) {\n          width: 0.65 * 800px;\n        }\n      }\n\n      &.is-x-tall {\n        // height: 60vw;\n\n        @media screen and (orientation: landscape) and (min-width: 800px) {\n          height: 0.575 * 800px;\n          transform: translateY(#{-0.075 * 800px});\n        }\n      }\n    }\n  }\n}\n\nsummary {\n  cursor: default;\n  padding-right: 1em;\n  display: inline-block;\n  font-weight: 700;