ll div,\n.my-marker-cluster-medium div,\n.my-marker-cluster-large div {\n  background-color: white;\n}\n\n.my-marker-cluster-small {\n  background-color: lighten($brand-pink,5%);\n  box-shadow: 9px 9px 12px -7px rgba(0,0,0,0.25) !important;\n}\n\n.my-marker-cluster-medium {\n  background-color: $brand-pink;\n  box-shadow: 9px 9px 12px -7px rgba(0,0,0,0.25) !important;\n}\n\n.my-marker-cluster-large {\n  background-color: darken($brand-pink,10%);\n  box-shadow: 9px 9px 12px -7px rgba(0,0,0,0.25) !important;\n}\n\n\n\n// Popup\n// .leaflet-container a.leaflet-popup-close-button {\n\n// }\n\n.leaflet-popup-close-button {\n  top: ms(0) !important;\n  right: ms(0) !important;\n  width: auto !important;\n  height: auto !important;\n  font: 30px/17px Tahoma, Verdana, sans-serif !important;\n}\n\n\n.leaflet-popup-content-wrapper {\n    max-width: $sidebar-width;\n    border: 1px solid $medium-gray;\n    border-bottom: none;\n    position: relative;\n    width: 100vw;\n    font-size: 1rem !important;\n    padding: ms(0) ms(-2);\n    // border-radius: 0 !important;\n    box-shadow: 13px 19px 12px -7px rgba(0,0,0,0.2) !important;\n\n    .popup--close {\n      position: absolute;\n      top: ms(0);\n      line-height: 1;\n      right: ms(0);\n      font-size: ms(4);\n      color: $gray;\n    }\n\n    .entry-title {\n      text-transform: capitalize;\n      font-size: ms(2);\n      font-weight: 400;\n      margin-bottom: ms(-1);\n    }\n\n    .entry-address {\n      font-size: ms(-1);\n      color: $gray;\n      text-transform: capitalize;\n\n      &::before {\n        content: '';\n        display: inline-block;\n        background-image: url(/assets/images/marker-icon-red.svg);\n        width: 0.75em;\n        height: 1em;\n        margin-right: .25em;\n        background-size: contain;\n        background-repeat: no-repeat;\n        background-position: left;\n      }\n    }\n\n    p {\n      margin: 0 0 ms(0);\n    }\n\n    .entry-links {\n      display: flex;\n      flex-direction: row;\n\n      > * + * {\n        margin-left: ms(-6);\n      }\n    }\n\n    .entry-link {\n      margin-bottom: ms(0);\n\n      a {\n        display: inline-block;\n        color: $dark-gray;\n        background-color: $medium-gray;\n        border-radius: 99999px;\n        line-height: ms(4);\n        padding: 0 ms(-2);\n      }\n\n    }\n\n    .entry-social {\n      margin-bottom: ms(0);\n      display: inline-block;\n      padding: ms(-4);\n      background-color: $dark-gray;\n      border-radius: 99999px;\n\n      svg {\n        display: block;\n        width: ms(0);\n        height: ms(0);\n\n        path {\n          fill: #fff\n        }\n      }\n\n      &__ebay {\n        padding: ms(-8);\n        svg {\n          width: ms(2);\n          height: ms(2);\n        }\n      }\n    }\n\n    .popup-footer {\n      border-top: 1px solid $light-gray;\n      padding-top: ms(0);\n    }\n}\n\n@media screen and (orientation: portrait) {\n  .leaflet-popup {\n    // display: none;\n  }\n}\n\n.leaflet-overlay-pane svg path {\n  stroke: $red !important;\n  fill: transparentize($red, 0.5) !important;\n}\n\n",".fade-enter-active, .fade-leave-active {\n  transition: opacity 0.5s;\n}\n.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {\n  opacity: 0;\n}\n\n.popup-enter-active, .popup-leave-active {\n  transition: transform 0.75s;\n  transition-timing-function: ease-in;\n}\n\n.popup-leave-active {\n  transition-timing-function: ease-out;\n  transition-delay: 0.5s;\n}\n\n.popup-enter, .popup-leave-to /* .fade-leave-active below version 2.1.8 */ {\n  transform: translateY(100%);\n  z-index: 999999;\n}\n\n.popup-leave, .popup-enter-to /* .fade-leave-active below version 2.1.8 */ {\n  transform: translateY(0);\n  z-index: 999998;\n}\n\n\n.slide-enter-active, .slide-leave-active {\n  transition: transform 0.75s;\n}\n\n.slide-leave-active {\n  z-index: 9999 !important;\n}\n\n.slide-enter /* .fade-leave-active below version 2.1.8 */ {\n  transform: translateX(100%);\n}\n\n.slide-leave-to {\n  transform: translateX(-100%);\n}\n\n.slide-from-right-enter-active, .slide-from-right-leave-active {\n  transition: transform 1.7s;\n}\n\n.slide-from-right-enter {\n  transform: translateX(1000px);\n}\n\n\n.expand-enter-active {\n  transition: flex 0.6s, opacity 0.6s;\n}\n\n.expand-leave-active {\n  transition: flex 0.6s, opacity 0.6s;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n  user-select: none;\n  pointer-events: none;\n  flex: 0 0 0px !important;\n}\n","@import '../../../node_modules/modularscale-sass/stylesheets/modularscale';\n@import 'fonts';\n@import 'variables';\n@import 'transitions';\n\n* {\n  font-family: 'Nunito Sans', sans-serif;\n}","$modularscale: (\n  base: 1rem,\n  ratio: 1.2\n);\n\n$sidebar-width: 360px;\n$sidebar-max-width: 30%;\n\n$light-gray: #f8f8f8;\n$medium-gray: #dfdfe5;\n$new-gray: #afafbf;\n$gray: #85898E;\n$dark-gray: #444455;\n\n$black: #222;\n\n$brand-green: #9bdbcd;\n$brand-pink: #efbfd3;\n$brand-blue: #48767f;\n\n$brand-light-blue: #abcee2;\n\n$blue: #aecdeb;\n$light-blue: #f0f6fc;\n$red: #df7839;\n$orange: #f4d582;\n$cream: #fef6dd;\n\n$base-timing: ease;\n$base-duration: 500ms;\n\n$base-radius: 0.25rem;\n\n$box-shadow: 0 0.75em 1.2em -0.125em rgba($black, 0.5), 0 0px 0 1px rgba($black, 0.02);"],"sourceRoot":""}]);
